import React from "react";
import { useState, useEffect } from "react";
import classNames from "classnames/bind";
import styles from "./AdjustRoom.module.scss";
import { GetUpdateRoom } from "api";
import { useModal } from "component/Modal/ModalContext";
import ListRoom from "component/RoomMonth/Room";
import {VNDFormatter} from "component/VNDFormatter";

const cx = classNames.bind(styles);

function AdjustRoomMonth({ props }) {
  const [data, setData] = useState({});
  const [deposit, setDeposit] = useState({});
  const [message, setMessage] = useState("");

  const { openModal } = useModal();

  const handleUpdateRoom = async () => {
    try {
      const response = await GetUpdateRoom(props._id, data);
      if (response) {
        setTimeout(() => {
          setMessage("");
        }, 3000);
        setMessage("Cp nht phng thnh cng!");
      } else {
        setMessage("Thm phng tht bi!");
      }
    } catch (error) {
      setMessage(error.message);
    }
  };

  const handleDeposit = (value) => {
    const rawValue = value.replace(/\./g, "");
    const numberValue = parseInt(rawValue, 10) || 0;
    return numberValue.toLocaleString();
  };
  useEffect(() => {
    // Cp nht `data`
    setData({
      hovaten: props.hovaten,
      GiaPhong: props.GiaPhong,
      chisodiencu: props.chisodiencu,
      chisodienmoi: props.chisodienmoi,
      chisonuoclanhcu: props.chisonuoclanhcu,
      chisonuoclanhmoi: props.chisonuoclanhmoi,
      chisonuocnongcu: props.chisonuocnongcu,
      chisonuocnongmoi: props.chisonuocnongmoi,
      sinhhoatchung: props.sinhhoatchung,
      ghichu: props.ghichu,
      loainuoc: props.loainuoc || "chiso",
      giakhoangnuoc: props.giakhoangnuoc,
      songuoikhoang: props.songuoikhoang,
    });
    setDeposit({
      giakhoangnuoc: props.giakhoangnuoc,
      GiaPhong: props.GiaPhong,
      sinhhoatchung: props.sinhhoatchung,
    });
  }, [props]);

  useEffect(() => {
    // Chy li khi `data` thay i
    ["GiaPhong", "sinhhoatchung"].forEach((key) => {
      if (data[key]) {
        const formattedValue = handleDeposit(data[key]);
        setDeposit((prev) => ({ ...prev, [key]: formattedValue }));
      }
    });
  }, [props]); // Theo di s thay i ca `data`

  const handleBackAgian = () => {
    return openModal(<ListRoom />);
  };

  return (
    <>
      <div className={cx("container")}>
        <h2 className={cx("title")}>Cp nht phng - {props.SoPhong}</h2>
        <form className={cx("form")} id="roomForm">
          {/* Nhm thng tin s phng v gi phng */}
          <div className={cx("formGroup")}>
            <div className={cx("inputGroup")}>
              <label className={cx("label")}>H v tn</label>
              <input
                value={data.hovaten || ""}
                onChange={(e) =>
                  setData((prev) => ({
                    ...prev,
                    hovaten: e.target.value,
                  }))
                }
                type="text"
                id="hovaten"
                required
                className={cx("input")}
              />
            </div>
            <div className={cx("inputGroup")}>
              <label className={cx("label")}>S Phng</label>
              <input
                value={props.SoPhong}
                type="number"
                id="SoPhong"
                disabled
                className={cx("input")}
              />
            </div>
          </div>

          {/* Nhm thng tin sinh hot chung */}
          <div className={cx("formGroup")}>
            <div className={cx("inputGroup")}>
              <label className={cx("label")}>Gi phng thng</label>
              <input
                value={data.GiaPhong || ""}
                onChange={(e) => {
                  setData((prev) => ({ ...prev, GiaPhong: e.target.value }));
                  setDeposit((prev) => ({
                    ...prev,
                    GiaPhong: e.target.value,
                  }));
                }}
                type="number"
                id="GiaPhong"
                required
                className={cx("input")}
              />
              <span>{VNDFormatter(deposit.GiaPhong) || ""}</span>
            </div>

            <div className={cx("inputGroup")}>
              <label className={cx("label")}>Ph sinh hot</label>
              <input
                value={data.sinhhoatchung || ""}
                onChange={(e) =>
                  setData((prev) => ({
                    ...prev,
                    sinhhoatchung: e.target.value,
                  }))
                }
                type="number"
                id="sinhhoatchung"
                required
                className={cx("input")}
              />
              <span>
                {VNDFormatter(data.sinhhoatchung) || ""}
              </span>
            </div>
          </div>

          {/* Nhm ch s in v nc */}
          <div className={cx("formGroup")}>
            <div className={cx("inputGroup")}>
              <label className={cx("label")}>Ch s in c</label>
              <input
                value={data.chisodiencu || ""}
                onChange={(e) =>
                  setData((prev) => ({ ...prev, chisodiencu: e.target.value }))
                }
                type="number"
                id="chisodien"
                required
                className={cx("input")}
              />
            </div>
            <div className={cx("inputGroup")}>
              <label className={cx("label")}>Ch s in mi</label>
              <input
                value={data.chisodienmoi || ""}
                onChange={(e) =>
                  setData((prev) => ({ ...prev, chisodienmoi: e.target.value }))
                }
                type="number"
                id="chisodien"
                required
                className={cx("input")}
              />
            </div>
          </div>

          {/* S la chn */}
          <div className={cx("formGroup", "w-100")}>
            <div className={cx("section")}>
              <label className={cx("label", "w-100")}>Loi s dng nc</label>
              <select
                value={data.loainuoc || "0"}
                onChange={(e) =>
                  setData((prev) => ({
                    ...prev,
                    loainuoc: e.target.value,
                  }))
                }
                id="loainuoc"
                className={cx("input", "selection")}
              >
                <option value="0">La chn ph hp</option>
                <option value="chiso">Dng nc tnh theo ch s</option>
                <option value="khoang">
                  Dng nc khong (
                  {VNDFormatter(data.giakhoangnuoc)}/ngi)
                </option>
              </select>
            </div>
          </div>

          {/* Nhm ch s nc nng v nc lnh */}

          {/* Nhm ch s nc lnh c v mi */}

          {data.loainuoc == "chiso" && (
            <>
              <div className={cx("formGroup")}>
                <div className={cx("inputGroup")}>
                  <label className={cx("label")}>Ch s nc nng c</label>
                  <input
                    value={data.chisonuocnongcu || ""}
                    onChange={(e) =>
                      setData((prev) => ({
                        ...prev,
                        chisonuocnongcu: e.target.value,
                      }))
                    }
                    type="number"
                    id="chisodien"
                    required
                    className={cx("input")}
                  />
                </div>
                <div className={cx("inputGroup")}>
                  <label className={cx("label")}>Ch s nc nng mi</label>
                  <input
                    value={data.chisonuocnongmoi || ""}
                    onChange={(e) =>
                      setData((prev) => ({
                        ...prev,
                        chisonuocnongmoi: e.target.value,
                      }))
                    }
                    type="number"
                    id="chisodien"
                    required
                    className={cx("input")}
                  />
                </div>
              </div>
              <div className={cx("formGroup")}>
                <div className={cx("inputGroup")}>
                  <label className={cx("label")}>Ch s nc lnh c</label>
                  <input
                    value={data.chisonuoclanhcu || ""}
                    onChange={(e) =>
                      setData((prev) => ({
                        ...prev,
                        chisonuoclanhcu: e.target.value,
                      }))
                    }
                    type="number"
                    id="chisonuoclanh"
                    required
                    className={cx("input")}
                  />
                </div>
                <div className={cx("inputGroup")}>
                  <label className={cx("label")}>Ch s nc lnh mi</label>
                  <input
                    value={data.chisonuoclanhmoi || ""}
                    onChange={(e) =>
                      setData((prev) => ({
                        ...prev,
                        chisonuoclanhmoi: e.target.value,
                      }))
                    }
                    type="number"
                    id="chisonuoclanh"
                    required
                    className={cx("input")}
                  />
                </div>
              </div>
            </>
          )}

          {data.loainuoc == "khoang" && (
            <>
              <div className={cx("formGroup")}>
                <div className={cx("inputGroup")}>
                  <label className={cx("label")}>S lng ngi </label>
                  <input
                    value={data.songuoikhoang || ""}
                    onChange={(e) =>
                      setData((prev) => ({
                        ...prev,
                        songuoikhoang: Number(e.target.value),
                      }))
                    }
                    type="number"
                    id="songuoikhoang"
                    required
                    className={cx("input")}
                  />
                </div>

                <div className={cx("inputGroup")}>
                  <label className={cx("label")}>Gi khong iu chnh</label>
                  <input
                    value={data.giakhoangnuoc || ""}
                    onChange={(e) => {
                      setData((prev) => ({
                        ...prev,
                        giakhoangnuoc: Number(e.target.value),
                      }));
                      setDeposit((prev) => ({
                        ...prev,
                        giakhoangnuoc: e.target.value,
                      }));
                    }}
                    type="number"
                    id="giakhoangnuoc"
                    required
                    className={cx("input")}
                  />
                  <span>
                    {<VNDFormatter value={deposit.giakhoangnuoc} /> || ""}
                  </span>
                </div>
              </div>
            </>
          )}

          <div className={cx("control")}>
            <button
              type="button"
              onClick={() => handleBackAgian()}
              className={cx("button")}
            >
              Quay li
            </button>
            <button
              type="button"
              onClick={() => handleUpdateRoom()}
              className={cx("button")}
            >
              Cp Nht
            </button>
          </div>
        </form>
        <p id="message" className={cx("message")}>
          {message}
        </p>
      </div>
    </>
  );
}

export default AdjustRoomMonth;
