import React, { useEffect, useRef, useState } from "react";
import html2canvas from "html2canvas";
import classNames from "classnames/bind";
import styles from "./Bill.module.scss";
import { useParams } from "react-router";
import { GetAllRoomsMonth } from "api";
import { VNDFormatter } from "component/VNDFormatter";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";



const cx = classNames.bind(styles);

function Bill({props}) {
  const { room } = useParams();
  const contentRef = useRef([]);
  const sliderRef = useRef(null);  // Thm ref cho slider
  const setWidth = useRef(null);  // Thm ref cho slider
  const [data, setData] = useState([]);  

  useEffect(() => {
    const handleKeyDown = async(e) => {
        if (e.key === "ArrowLeft") {
          sliderRef.current.slickPrev();  // Gi slickPrev()  chuyn sang slide trc
        } else if (e.key === "ArrowRight") {
          sliderRef.current.slickNext();  // Gi slickNext()  chuyn sang slide tip theo
        } else if(e.key === "Enter") {
            
            const checked = window.confirm("Bn c mun to phiu ny khng")
            if(checked) {
              alert("H thng cn thi gian render nh vui lng i khong 3-5s")
              const download = await handleDownloadAll()
              if(download) alert("render thnh cng")
            }else{
              alert("Hy thnh cng")
            }
        }
      };
  
      // Lng nghe s kin khi bn phm c nhn
      window.addEventListener("keydown", handleKeyDown);
  
      // Cleanup event listener khi component unmount
      return () => {
        window.removeEventListener("keydown", handleKeyDown);
      };
  }, []);

  useEffect(() => {
    const GetRoom = async () => {
      try {
        const response = await GetAllRoomsMonth();
        if (response) {
          const filtered = response.data.filter(
            (item) => item.SoPhong === Number(room) || item.SoPhong === Number(props)
          );
          setData(filtered.length > 0 ? filtered : response.data.filter((item) => item.GiaPhong > 0));
        }
      } catch (error) {
        console.error("Error fetching rooms:", error);
      }
    };
    GetRoom();
  }, [room]);


  const handleDownloadAll = async () => {
    const originalWidth = setWidth.current.style.width;

    setWidth.current.style.width = "100%"
    const promises = contentRef.current.map(async (content, index) => {
          const originalPadding = content.style.padding;
          content.style.padding = "24px";
          const canvas = await html2canvas(content, {
            scale: 2,
            useCORS: true,
          });
          const imgData = canvas.toDataURL("image/png");

          // To mt lin kt ti v cho tng nh
          const link = document.createElement("a");
          link.href = imgData;
          link.download = `hoa-don-phong-${index}.png`;
          link.click();
          content.style.padding = originalPadding;
    });
    
    // Ch tt c cc Promise hon thnh (ti tt c nh)
    await promises;
    
    setWidth.current.style.width = originalWidth
    return true
  };

  
  const sliderSettings = {
    dots: false,
    infinite: false,
    speed: 800,
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: true, // Kch hot arrow
    prevArrow: <div className="prev-arrow" />,
    nextArrow: <div className="next-arrow" />,
    centerMode: true, //  gi slide gia
    centerPadding: "0px", // khng padding thm
  };
  

  return (
    <div ref={setWidth} className={cx("invoice-container")}>
      <div className={cx("tutorial")}>
        <span> ti ha n bn hy nhn nt Enter, h thng s ti ha n v my<br></br> duy chuyn gia cc nh nhn nt mi tn qua tri trn bn phm v mi tn qua phi trn bn phm</span>
      </div>
      {data.length > 0 && (
        <Slider {...sliderSettings} ref={sliderRef}>
          {data.map((item,index) => {
            console.log(item);
            const dientieuthu = item.chisodienmoi - item.chisodiencu;
            const nuoctieuthu =
              item.chisonuoclanhmoi -
              item.chisonuoclanhcu +
              item.chisonuocnongmoi -
              item.chisonuocnongcu;
            const dieukien1 = nuoctieuthu * 20000;
            const dieukien2 = item.songuoikhoang * item.giakhoangnuoc;
            const coastNuoc = item.loainuoc == "chiso" ? dieukien1 : dieukien2;
            const coastDien = dientieuthu > 0 ? dientieuthu * 4000 : 0;
            const total =
              coastNuoc + coastDien + item.sinhhoatchung + item.GiaPhong;
            const Sophong = String(item.SoPhong).padStart(3, "0");

            return (
              <div key={index} ref={(el) => (contentRef.current[item.SoPhong] = el)} className={cx("container_billhata")} >
                <div className={cx("header")}>
                  <div className={cx("left")}>
                    <p>
                      <strong>HATA HOME STAY</strong>
                    </p>
                    <p>128/1A Trn Ph</p>
                  </div>
                  <div className={cx("center")}>
                    <p>PHIU THU TIN TR</p>
                  </div>
                  <div className={cx("right")}>
                    <span>Phng s :</span>
                    <span className={cx("line", "w-32", "text-center","customer-text")}>
                      {Sophong}
                    </span>
                  </div>
                </div>

                <div className={cx("info")}>
                  <span>Thi gian :</span>
                  <span
                    className={cx(
                      "line",
                      "w-64",
                      "text-center",
                      "customer-text"
                    )}
                  >
                    Thng {new Date().getMonth() + 1}/2025
                  </span>
                </div>
                <div className={cx("info")}>
                  <span>H v tn :</span>
                  <span
                    className={cx(
                      "line",
                      "w-64",
                      "text-center",
                      "customer-text"
                    )}
                  >
                    {item.hovaten || "Ch Phng"}
                  </span>
                </div>

                <table>
                  <thead>
                    <tr>
                      <th>STT</th>
                      <th>Ni dung</th>
                      <th>Ch s u</th>
                      <th>Ch s cui</th>
                      <th>S lng</th>
                      <th>n gi</th>
                      <th>Thnh tin</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr className={cx("customer-text")}>
                      <td>1</td>
                      <td>Tin Phng</td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td>{VNDFormatter(item.GiaPhong)}</td>
                    </tr>
                    <tr>
                      <td>2</td>
                      <td>Tin in</td>
                      <td>{item.chisodiencu}</td>
                      <td>{item.chisodienmoi}</td>
                      <td>{dientieuthu}</td>
                      <td>{VNDFormatter(4000)}</td>
                      <td>{VNDFormatter(coastDien)}</td>
                    </tr>
                    {item.loainuoc === "chiso" ? (
                      <tr>
                        <td>3</td>
                        <td>Tin Nc</td>
                        <td>{item.chisonuoclanhcu + item.chisonuocnongcu}</td>
                        <td>{item.chisonuoclanhmoi + item.chisonuocnongmoi}</td>
                        <td>{nuoctieuthu}</td>
                        <td>{VNDFormatter(20000)}</td>
                        <td>{VNDFormatter(coastNuoc)}</td>
                      </tr>
                    ) : (
                      <tr>
                        <td>3</td>
                        <td>Tin Nc</td>
                        <td></td>
                        <td></td>
                        <td>{item.songuoikhoang} ngi</td>
                        <td>{VNDFormatter(item.giakhoangnuoc)}</td>
                        <td>{VNDFormatter(coastNuoc)}</td>
                      </tr>
                    )}
                    <tr>
                      <td>4</td>
                      <td>Tin Wifi+Rc+Xe</td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td>{VNDFormatter(item.sinhhoatchung)}</td>
                    </tr>
                    <tr>
                      <td>5</td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                    </tr>
                    <tr>
                      <td>6</td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                    </tr>
                    <tr>
                      <td>7</td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                    </tr>

                    <tr>
                      <td></td>
                      <td>
                        <strong className={cx("total")}>TNG CNG</strong>
                      </td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td>
                        <strong>{VNDFormatter(total)}</strong>
                      </td>
                    </tr>
                  </tbody>
                </table>

                <div className={cx("footer")}>
                  <div className={cx("text")}>
                    <span>K nhn</span>
                  </div>
                  <div className={cx("image")}>
                    <img src="/EDIT.png" alt="signature" />
                  </div>
                </div>
              </div>
            );
          })}
        </Slider>
      )}
    </div>
  );
}

export default Bill;
