// Hm ly gi Vit Nam
export function getVietnamTime() {
  const now = new Date();
  const utc = now.getTime() + now.getTimezoneOffset() * 60000;
  return new Date(utc + 7 * 3600000);
}

// Hm nh dng ngy theo mi gi Vit Nam
export function formatVietnamDate(dateObj) {
  console.log("du lieu nhan duoc", dateObj);
  const day = String(dateObj.getDate()).padStart(2, "0");
  const month = String(dateObj.getMonth() + 1).padStart(2, "0");
  const year = dateObj.getFullYear();
  return `${year}-${month}-${day}`;
}

export function convertToISODate(dateStr) {
  if (!/^\d{2}-\d{2}-\d{4}$/.test(dateStr)) return "";
  const [day, month, year] = dateStr.split("-");
  return `${year}-${month}-${day}`;
}

export const convertToStandardDateFormat = (dateStr, timeStr) => {
  // Tch ngy thnh ngy, thng, nm
  const [day, month, year] = dateStr.split("/");

  // Chuyn i thnh nh dng yyyy-mm-dd
  const formattedDate = `${day.padStart(2, "0")}-${month.padStart(
    2,
    "0"
  )}-${year}`;

  let formattedDateTime;
  // Kt hp ngy vi gi
  if (timeStr) {
    formattedDateTime = `${formattedDate} ${timeStr}`;
  } else {
    formattedDateTime = `${formattedDate}`;
  }

  return formattedDateTime;
};

export const formatDateValue = (datetime) => {
  if (!datetime) return "";
  const [year, date, day] = datetime.split(" ")[0].split("-"); // Chia chui theo du "-"

  return day + "-" + date + "-" + year; // nh dng li thnh d-m-Y
};

// Ly gi nh dng hh:mm
export const formatTimeValue = (datetime) => {
  if (!datetime) return "";
  const [, time] = datetime.split(" ");
  return time?.substring(0, 5) || "";
};

export const CurrentDate = () => {
  const today = new Date();

  // Hm thm s 0 pha trc nu < 10
  const pad = (n) => n.toString().padStart(2, "0");

  const day = pad(today.getDate());
  const month = pad(today.getMonth() + 1);
  const year = today.getFullYear();

  const formattedDate = `${day}/${month}/${year}`;

  return formattedDate;
};

export const CalculateEndDate = (monthsToAdd) => {
  // nh dng ngy DD/MM/YYYY
  const formatDate = (date) => {
    const pad = (n) => n.toString().padStart(2, "0");
    return `${pad(date.getDate())}/${pad(date.getMonth() + 1)}/${date.getFullYear()}`;
  };

  const calculateEndDate = (monthsToAdd) => {
    const startDate = new Date(); // Ngy bt u
    const day = startDate.getDate();
    const month = startDate.getMonth();
    const year = startDate.getFullYear();

    // To ngy mi cng thng
    const newMonth = month + monthsToAdd;
    const newDate = new Date(year, newMonth, 1); // Tm thi t l ngy u thng mi

    // Tnh s ngy ti a trong thng mi
    const maxDay = new Date(newDate.getFullYear(), newDate.getMonth() + 1, 0).getDate();

    // Nu ngy bt u ln hn s ngy ca thng mi => dng ngy cui thng
    newDate.setDate(Math.min(day, maxDay));

    return formatDate(newDate);
  };

  return calculateEndDate(monthsToAdd);
};
