import React, { useEffect, useState } from 'react';
import styles from './ListWater.module.scss';
import classNames from 'classnames/bind';
import { fetchWaterList, updateWater } from 'api';
import { useModal } from 'component/Modal/ModalContext';
import Inventory from 'component/WaterManager/Inventory';

const cx = classNames.bind(styles);

function ListWater() {
    const { openModal } = useModal();
    const [search, setSearch] = useState("");
    const [waterList, setWaterList] = useState([]);        // D liu hin th
    const [originalList, setOriginalList] = useState([]);  // D liu gc
    const [currentPage, setCurrentPage] = useState(1);
    const itemsPerPage = 40;

    // Ly danh sch nc
    const getWaterList = async () => {
        try {
            const data = await fetchWaterList();
            if (data) {
                setOriginalList(data);  // Lu bn gc
                setWaterList(data);     // Hin th mc nh
            }

            return data
        } catch (error) {
            console.error("Li khi ly danh sch nc:", error);
        }
    };

    useEffect(() => {
        getWaterList();
    }, []);


    const handleSearch = (e) => {   
        if(search) {
            const filtered = originalList.filter((item) => item.tenNuoc.toLowerCase().includes(search.toLowerCase()));
            setWaterList(filtered); 
            return
        }

        setWaterList(originalList); // Nu khng c g th hin th li danh sch gc
    }

    useEffect(() => {
        handleSearch(search)
    }, [search])

  

    // Tnh ton phn trang
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = waterList.slice(indexOfFirstItem, indexOfLastItem);

    const nextPage = () => {
        if (currentPage < totalPages()) {
            setCurrentPage(currentPage + 1);
        }
    };

    const prevPage = () => {
        if (currentPage > 1) {
            setCurrentPage(currentPage - 1);
        }
    };

    const totalPages = () => {
        return Math.ceil(waterList.length / itemsPerPage);
    };

    // Cp nht s lng nc khi bn
    const handleAddWater = async (name, type) => { 
        const amount = prompt("Nhp s lng nc  bn?\nNu mun tr s lng -(s lng mun tr)");
        if (amount) {
            let object
            if(type == "nhap") {
                object = {
                    tenNuoc: name,    
                    nhap: amount || 0,
                };
            }else if(type == "ban") {
                object = {
                    tenNuoc: name,    
                    soLuong: amount || 0,    
                };
            }
            const updated = await updateWater(object);
            if (updated) {
                const response = await getWaterList();  // Cp nht li danh sch
                alert("Np d liu thnh cng")
                
                if (search) {
                    // T ng lc li khi c tm kim
                    const filtered = response.filter((item) =>
                        item.tenNuoc.toLowerCase().includes(search.toLowerCase())
                    );
                    setWaterList(filtered);
                }
            }
        }
    };
     
    const handleInventory = () => {
        openModal(<Inventory />)
    }

    return (
        <div>
            <div className={cx("search")}>
                <input 
                    value={search || ""} 
                    onChange={(e) => setSearch(e.target.value)} 
                    type="text" 
                    placeholder="Tm kim..." 
                    className={cx("search-input")} 
                />
                <button onClick={(e) => handleInventory()} className={cx("search-button")}>Kim hng</button>
            </div>
            <table className={cx("water-table-history-table")}>
                <thead>
                    <tr>
                        <th>STT</th>
                        <th>Loi Nc</th>
                        <th>S lng u</th>
                        <th>S lng nhp</th>
                        <th>S lng bn</th>
                        <th>S lng cui</th>
                    </tr>
                </thead>
                <tbody className={cx("water-table-data")}>
                    {currentItems.length > 0 ? (
                        currentItems.map((item, index) => (
                            <tr key={index}>
                                <td>{indexOfFirstItem + index + 1}</td>
                                <td>{item.tenNuoc}</td>
                                <td>{item.soLuongDau}</td>
                                <td>{item.nhap}
                                    <a
                                        className={cx("plus-minus-button")}
                                        onClick={() => handleAddWater(item.tenNuoc, "nhap")}
                                    >
                                        +
                                    </a>    
                                </td>
                                <td>
                                    {item.ban}{" "}
                                    <a
                                        className={cx("plus-minus-button")}
                                        onClick={() => handleAddWater(item.tenNuoc, "ban")}
                                    >
                                        +
                                    </a>
                                </td>
                                <td>{item.thucTeCuoiKy}</td>
                            </tr>
                        ))
                    ) : (
                        <tr>
                            <td colSpan="8" style={{ textAlign: 'center', padding: '20px', fontStyle: 'italic' }}>
                                Khng c bng no c ghi
                            </td>
                        </tr>
                    )}
                </tbody>
            </table>

            {/* Pagination Controls */}
            <div className={cx("water-table-pagination")}>
                <div className={cx("water-table-notification")}>
                    <span>Trang {currentPage} of {totalPages()}</span>
                </div>
                <div className={cx("water-table-controls")}>
                    <button onClick={prevPage} disabled={currentPage === 1}>
                        Trang trc
                    </button>
                    <button onClick={nextPage} disabled={currentPage === totalPages()}>
                        Trang sau
                    </button>
                </div>
            </div>
        </div>
    );
}

export default ListWater;
