import React, { useEffect, useState, useRef } from "react";
import flatpickr from "flatpickr";
import "flatpickr/dist/flatpickr.min.css";
import classNames from "classnames/bind";
import styles from "./Room.module.scss";
import { formatTimeValue, formatDateValue } from "../dateUtils";
import { useDebounceHandler } from "../debounce";

const cx = classNames.bind(styles);

const Room = ({ room, handleInputChange, setCurrentTime, calculate }) => {
  const debounce = useDebounceHandler();
  const [tempDates, setTempDates] = useState({});
  const checkDateRef = useRef({});
  const originalRef = useRef({});

  // Hm x l thay i input
  const handleChange = (type, roomNumber, e) => {
    const newValue = e.target.value;

    // Ly gi tr ban u t ref hoc tnh ton t room nu cha c gi tr
    const originalValue = originalRef.current[`${type}-${roomNumber}`] ??
      (type === "date" ? formatDateValue(room.checkInDate) : formatTimeValue(room.checkInDate));

    // Cp nht gi tr mi vo originalRef  theo di s thay i
    originalRef.current[`${type}-${roomNumber}`] = newValue;

    // Cp nht state (tempDates) vi gi tr mi
    setTempDates((prev) => ({
      ...prev,
      [`${type}-${roomNumber}`]: newValue,
    }));

    // Gi debounce sau khi cp nht state
    debounce(
      `${type}-${roomNumber}`,
      newValue,
      originalValue,
      () => {
        // Truyn ng d liu vo handleInputChange
        handleInputChange(roomNumber, {
          type,
          value: newValue,
          ...(type === "checkTimeDate"
            ? {
                date: tempDates[`checkInDate-${room.roomNumber}`] ?? formatDateValue(room.checkInDate),
              }
            : {
                time: tempDates[`checkTimeDate-${room.roomNumber}`] ?? formatTimeValue(room.checkInDate),
              }),
        });
      }
    );
  };

  // Hm khi to flatpickr
  const initFlatpickr = () => {
    if (!checkDateRef.current[room.roomNumber]) return;

    const refs = checkDateRef.current[room.roomNumber];

    flatpickr(refs.checkindate, {
      dateFormat: "d-m-Y",
     
      onChange: () => handleChange("checkInDate", room.roomNumber, { target: { value: refs.checkindate.value } }), // Gi handleChange khi chn ngy
    });

    flatpickr(refs.checkoutdate, {
      dateFormat: "d-m-Y",
      //onChange: () => handleChange("checkOutDate", room.roomNumber, { target: { value: refs.checkoutdate.value } }), // Gi handleChange khi chn ngy
    });

    flatpickr(refs.checkintime, {
      enableTime: true,
      noCalendar: true,
      dateFormat: "H:i",
      time_24hr: true,
      minuteIncrement: 60,
      onChange: () => handleChange("checkTimeDate", room.roomNumber, { target: { value: refs.checkintime.value } }), // Gi handleChange khi chn thi gian
    });

    flatpickr(refs.checkouttime, {
      enableTime: true,
      noCalendar: true,
      dateFormat: "H:i",
      time_24hr: true,
      minuteIncrement: 60,
      
    });
  };

  useEffect(() => {
    initFlatpickr();
  }, [room.roomNumber]);

  // Hm  gn ref cho cc input
  const setRef = (el, key) => {
    if (!checkDateRef.current[room.roomNumber]) {
      checkDateRef.current[room.roomNumber] = {};
    }
    if (el) {
      checkDateRef.current[room.roomNumber][key] = el;
    }
  };

  // Hm tnh tin
  const handleCalculate = () => {
    calculate(room.roomNumber, checkDateRef);
  };

  return (
    <div className={cx("room")}>
      <h3>Phng {String(room.roomNumber).padStart(3, "0")}</h3>

      {/* Check-in */}
      <div className={cx("time-container")}>
        <input
          ref={(el) => setRef(el, "checkindate")}
          type="text"
          id={`checkin-date-${room.roomNumber}`}
          className={cx("time-picker")}
          value={tempDates[`checkInDate-${room.roomNumber}`] ?? formatDateValue(room.checkInDate)}
          onChange={(e) => handleChange("checkInDate", room.roomNumber, e)}
        />
        <input
          ref={(el) => setRef(el, "checkintime")}
          type="text"
          id={`checkin-time-${room.roomNumber}`}
          className={cx("time-picker")}
          value={tempDates[`checkTimeDate-${room.roomNumber}`] ?? formatTimeValue(room.checkInDate)}
          onChange={(e) => handleChange("checkTimeDate", room.roomNumber, e)}
        />
      </div>

      {/* Check-out */}
      <div className={cx("time-container")}>
        <input
          ref={(el) => setRef(el, "checkoutdate")}
          type="text"
          id={`checkout-date-${room.roomNumber}`}
          className={cx("time-picker")}
          
          
        />
        <input
          ref={(el) => setRef(el, "checkouttime")}
          type="text"
          id={`checkout-time-${room.roomNumber}`}
          className={cx("time-picker")}
         
         
        />
      </div>

      {/* Buttons */}
      <div className={cx("btn-container")}>
        <div className={cx("btn-top")}>
          <button className={cx("check-in")} onClick={() => setCurrentTime("checkin", room.roomNumber, checkDateRef)}>
            Nhn phng
          </button>
          <button className={cx("check-out")} onClick={() => setCurrentTime("checkout", room.roomNumber, checkDateRef)}>
            Tr phng
          </button>
        </div>
        <div className={cx("btn-bottom")}>
          <button className={cx("btn-calculate")} onClick={handleCalculate}>
            Tnh tin
          </button>
        </div>
      </div>
    </div>
  );
};

export default Room;
