import React from "react";
import styles from "./Telegram.module.scss";
import classNames from "classnames/bind";

const cx = classNames.bind(styles);

const Form2 = () => {
  return (
    <>
      <div className={cx("container")}>
      <div className={cx("image-wrapper")}>
        <img
          src="./telegram.png"
          alt="M QR Telegram"
          className={cx("image")}
        />
      </div>
        <span className={cx("header")}>
          {" "}
          <strong>
            Cho mng bn n vi bot tnh tin phng HataHomeStay trn Telegram!
          </strong>
        </span>

        <span className={cx("instruction")}>
          Bot ny c to ra  gip bn gi ch s in, nc hng thng mt
          cch <strong>nhanh chng</strong> v <strong>chnh xc</strong>. Di
          y l hng dn chi tit:
        </span>

        <span className={cx("step")}>
           <strong>Bc 1:</strong> M ng dng <em>Qut m QR</em> trn in
          thoi ca bn.
        </span>

        <span className={cx("step")}>
           <strong>Bc 2:</strong> Qut m QR hin th trn mn hnh hoc t
          hng dn. Telegram s t ng m bot c tn <em>HATAHOMESTAY_BOT</em>
          .
        </span>

        <span className={cx("step")}>
           <strong>Bc 3:</strong> Nhn nt <code>Start</code>  bt u s
          dng bot.
        </span>

        <span className={cx("instruction")}>
          Sau khi khi ng bot, bn c th nhp s phng bng cch gi
          lnh theo mu:
        </span>

        <span className={cx("example")}>
          <code>/001</code>
        </span>

        <span className={cx("detail")}>
          Trong  <code>001</code> l s phng. Khi bn gi lnh ny:
          <br /> Bot s kim tra xem phng  c tn ti hay khng.
          <br /> Nu phng <strong>cha c thi gian check-in</strong>, bot s
          t ng cp nht thi gian v thng bo cho bn v ch cn gi li lnh c h thng s t ng tnh ton cho bn.
          <br /> Nu phng <strong> c thi gian</strong>, bot s t ng
          tnh tin da trn s liu  c trong danh sch v gi li thng tin thanh ton chnh xc.
          <br /> Nu mun reset thi gian nhn phng ch cn gi lnh <strong>/sophong r, v d: /001 r</strong>, lnh ny h thng s t ng xa thi gian checkin v sn sng ch lnh tip theo.
        </span>

        <span className={cx("footer")}>
        Bot HataHomeStay c pht trin bi <strong>Hu c</strong>. Cm n
        bn  s dng!
      </span>
      </div>
    </>
  );
};

const Form1 = () => {
  return (
    <div className={cx("container")}>
      <div className={cx("image-wrapper")}>
        <img
          src="./telegram.png"
          alt="M QR Telegram"
          className={cx("image")}
        />
      </div>

      <span className={cx("header")}>
        {" "}
        <strong>
          Cho mng bn n vi bot in nc HataHomeStay trn Telegram!
        </strong>
      </span>

      <span className={cx("intro")}>
        Bot ny gip bn gi ch s in, nc hng thng d dng v chnh xc.
        Di y l hng dn chi tit nh!
      </span>

      <span className={cx("spacer")} />

      <span className={cx("step")}>
        <strong>Bc 1:</strong> Dng in thoi m ng dng{" "}
        <strong>Qut m QR</strong>.
      </span>

      <span className={cx("step")}>
        <strong>Bc 2:</strong> Qut m QR hin th trn mn hnh hoc t hng
        dn.
      </span>

      <span className={cx("instruction")}>
        Sau khi qut xong, Telegram s m ra bot c tn{" "}
        <em>HATAHOMESTAY_BOT</em>.
      </span>

      <span className={cx("step")}>
        <strong>Bc 3:</strong> Nhn nt <code>Start</code>  bt u s dng
        bot.
      </span>

      <span className={cx("instruction")}>
        Sau khi nhn Start, bn c th bt u nhp ch s in nc mi thng.
      </span>

      <span className={cx("step")}>
        <strong>Bc 4:</strong> Gi thng tin theo ng mu sau:
      </span>

      <span className={cx("example")}>
        <code>001 15 223 1523</code>
      </span>

      <span className={cx("note")}> Gii thch:</span>
      <span className={cx("detail")}>
        - <code>001</code>: S phng
        <br />- <code>15</code>: S nc nng mi
        <br />- <code>223</code>: S nc lnh mi
        <br />- <code>1523</code>: S in mi
      </span>
      <span className={cx("step")}>
         Bn c th gi nhiu phng cng lc bng cch xung dng, v d:
      </span>

      <span className={cx("example")}>
        <code>001 15 223 1523</code>
        <br />
        <code>002 14 210 1490</code>
        <br />
        <code>003 12 190 1400</code>
      </span>

      <span className={cx("instruction")}>
        Mi dng l thng tin ca 1 phng. Nh nhp ng th t nh!
      </span>

      <span className={cx("response")}>
        Sau khi gi, bot s phn hi nh: <em>" cp nht cho phng 001"</em>,
        "phng 002", v.v...
      </span>

      <span className={cx("instruction")}>
        Bot s t ng lu li. Bn khng cn nhp li hay lo qun!
      </span>

      <span className={cx("step")}>
        <strong>Bc 5:</strong> Quay li trang danh sch phng trn h thng.
      </span>

      <span className={cx("instruction")}>
        Cc ch s va nhp s c cp nht t ng. Bn c th kim tra li v
        to ha n ngay.
      </span>

      <span className={cx("spacer")} />

      <span className={cx("footer")}>
        Bot HataHomeStay c pht trin bi <strong>Hu c</strong>. Cm n
        bn  s dng!
      </span>
    </div>
  );
};
function Telegram({ props }) {
  return (
    <>
      {props === 1 && <Form1 />}
      {props === 2 && <Form2 />}
      {props === "all" && (
        <>
          <Form1 />
          <Form2 />
        </>
      )}
    </>
  );
}

export default Telegram;
