
import React from 'react';
import styles from './Inventory.module.scss';
import classNames from 'classnames/bind';
import { useEffect, useState } from 'react';
import { useModal } from 'component/Modal/ModalContext';
import ListWater from './ListWater';
import { TestInventory,TestInventory2 } from 'api';


const cx = classNames.bind(styles)

function Inventory() {

    const [waterList, setWaterList] = useState([]);        // D liu hin th
    const [currentPage, setCurrentPage] = useState(1);
    const itemsPerPage = 40;
    const {openModal} = useModal();

    // Ly danh sch nc
    const getWaterList = async () => {
        console.log("ham da duoc goi")
        try {
            const data = await TestInventory();
            if (data) {
                console.log(data)
                setWaterList(data);     // Hin th mc nh
            }

            return data
        } catch (error) {
            console.error("Li khi ly danh sch nc:", error);
        }
    };

    useEffect(() => {
        getWaterList();
    }, []);

    // Tnh ton phn trang
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = waterList.slice(indexOfFirstItem, indexOfLastItem);
    

    const nextPage = () => {
        if (currentPage < totalPages()) {
            setCurrentPage(currentPage + 1);
        }
    };

    const prevPage = () => {
        if (currentPage > 1) {
            setCurrentPage(currentPage - 1);
        }
    };

    const totalPages = () => {
        return Math.ceil(waterList.length / itemsPerPage);
    };


    const HandleBack = () => {  
        
        openModal(<ListWater />); // M modal vi ni dung l component ListWater
    }

    const resetWt = async() => { 
        const password = prompt(" reset bn cn nhp mt khu: ")
        if(password !== "0786566176") return alert("Mt khu khng ng")
        const data = await TestInventory2();
        if(data) {
            openModal(<ListWater />); // M modal vi ni dung l component ListWater
        }
    }


    return (
        <div>
            <div className={cx("search")}>

                <button onClick={(e) => HandleBack()} className={cx("search-button")}>Quay li</button>
                <button onClick={(e) => resetWt()} className={cx("search-button", "news")}>Lm mi</button>
            </div>
            <table className={cx("water-table-history-table")}>
                <thead>
                    <tr>
                        <th>STT</th>
                        <th>Loi Nc</th>
                        <th>S lng u</th>
                        <th>S lng bn</th>
                        <th>S lng cui</th>
                    </tr>
                </thead>
                <tbody className={cx("water-table-data")}>
                    {currentItems.length > 0 ? (
                        currentItems.map((item, index) => (
                            
                            <tr key={index}>
                                <td>{index + 1}</td>
                                <td>Nc {item.tenNuoc}</td>
                                <td><span>{item.soLuongDau}</span></td>
                                <td><span>{item.ban}</span></td>
                                <td><span>{item.thucTeCuoiKy}</span></td>
                                
                            </tr>
                        ))
                    ) : (
                        <tr>
                            <td colSpan="8" style={{ textAlign: 'center', padding: '20px', fontStyle: 'italic' }}>
                                Khng c bng no c ghi
                            </td>
                        </tr>
                    )}
                </tbody>
            </table>

            {/* Pagination Controls */}
            <div className={cx("water-table-pagination")}>
                <div className={cx("water-table-notification")}>
                    <span>Trang {currentPage} of {totalPages()}</span>
                </div>
                <div className={cx("water-table-controls")}>
                    <button onClick={prevPage} disabled={currentPage === 1}>
                        Trang trc
                    </button>
                    <button onClick={nextPage} disabled={currentPage === totalPages()}>
                        Trang sau
                    </button>
                </div>
            </div>
        </div>
    );
}

export default Inventory;