import React, { useEffect, useState } from "react";
import styles from "./PaymentInfo.module.scss";
import classNames from "classnames/bind";
import { useModal } from "component/Modal/ModalContext";

import Bill from "component/Bill/Bill";
import { FaPrint } from 'react-icons/fa';
import Menu from "./Menu";
const cx = classNames.bind(styles);

function PayMentInfo({ props }) {
  const { openModal } = useModal();
  const [choice, setChoice] = useState(0);

  useEffect(() => {
    if(choice === 2) {
      openModal(<Bill />)
    }
  }, [choice])
  

  return (
    <div className={cx("invoice-container")}>
      <h2 className={cx("invoice-title")}>Danh sch ha n thanh ton</h2>
      <Menu props={props} />
      {choice === 0 && (
        <div className={cx("choice")}>
          <label>
            <input
              type="checkbox"
              checked={choice === 1}
              onChange={() => setChoice(1)}
            />
            In phiu ni b<br></br>
            (S hin th tt c cc thng s ca nhm mc ch qun l)<br></br>
            (C th to phiu cho tng phng)
          </label>  
          <label>
            <input
              type="checkbox"
              checked={choice === 2}
              onChange={() => setChoice(2)}
            />
            In phiu cho Khch hng<br></br>
            (Ch c cc phng c khch s c to phiu)<br></br>
            (Khng th to phiu cho tng phng ring l)<br></br>
            (Nhn Enter  Download Tt C)<br></br>
          </label>
          
          
        </div>
      )}

      {choice === 1 && (
        <div className={cx("invoice-list")}>
          {props
            .filter((item) => item.GiaPhong >= 0)
            .map((item) => {
              const dientieuthu = item.chisodienmoi - item.chisodiencu;
              const nuoctieuthu =
                item.chisonuoclanhmoi -
                item.chisonuoclanhcu +
                item.chisonuocnongmoi -
                item.chisonuocnongcu;
              const coastNuoc = nuoctieuthu > 0 ? nuoctieuthu * 20000 : 0;
              const coastDien = dientieuthu > 0 ? dientieuthu * 4000 : 0;
              const total =
                coastNuoc + coastDien + item.sinhhoatchung + item.GiaPhong;
              return (
                <div className={cx("invoice-card")}>
                  <div className={cx("print")} onClick={(e) => openModal(<Bill props={item.SoPhong} />)}><FaPrint /></div>
                  <h3 className={cx("customer-name")}>
                    {item.hovaten} - {item.SoPhong}
                  </h3>
                  <div className={cx("invoice-info")}>
                    <span className={cx("divider")}>
                      -----------------------------------------------
                    </span>
                    <div className={cx("invoice-info-item")}>
                      <div className={cx("info-line")}>
                        in mi: {item.chisodienmoi} kWh
                      </div>
                      <div className={cx("info-line")}>
                        in c: {item.chisodiencu} kWh
                      </div>
                    </div>
                    <span className={cx("divider")}>
                      --------------------------------------------------
                    </span>
                    <div className={cx("invoice-info-item")}>
                      <div className={cx("info-line")}>
                        Nc lnh mi: {item.chisonuoclanhmoi} m
                      </div>
                      <div className={cx("info-line")}>
                        Nc lnh c: {item.chisonuoclanhcu} m
                      </div>
                    </div>
                    <div className={cx("invoice-info-item")}>
                      <div className={cx("info-line")}>
                        Nc nng mi: {item.chisonuocnongmoi} m
                      </div>
                      <div className={cx("info-line")}>
                        Nc nng c: {item.chisonuocnongcu} m
                      </div>
                    </div>
                    <div className={cx("invoice-info-item")}>
                      <div className={cx("info-line")}>
                        Tng nc mi:{" "}
                        {item.chisonuoclanhmoi + item.chisonuocnongmoi} m
                      </div>
                      <div className={cx("info-line")}>
                        Tng nc c:{" "}
                        {item.chisonuoclanhcu + item.chisonuocnongcu} m
                      </div>
                    </div>

                    <span className={cx("divider")}>
                      --------------------------------------------------
                    </span>
                    <div className={cx("invoice-info-item")}>
                      <div className={cx("info-line")}>
                        Gi phng: {item.GiaPhong.toLocaleString()}
                      </div>
                      <div className={cx("info-line")}>
                        Ph Sinh hot: {item.sinhhoatchung.toLocaleString()}
                      </div>
                    </div>
                    <div className={cx("invoice-info-item")}>
                      <div className={cx("info-line")}>
                        in tiu th: {dientieuthu >= 0 ? dientieuthu : 0} kWh
                      </div>
                      <div className={cx("info-line")}>
                        Nc tiu th: {nuoctieuthu >= 0 ? nuoctieuthu : 0} m
                      </div>
                    </div>
                    <span className={cx("divider")}>
                      --------------------------------------------------
                    </span>
                    <div className={cx("invoice-info-item")}>
                      <div className={cx("info-line")}>
                        Tin in: {coastDien.toLocaleString()}<br></br>Gi
                        in:{" "}
                        {dientieuthu >= 0 ? `${dientieuthu} x 4.000` : "0"}
                      </div>
                      <div className={cx("info-line")}>
                        Tin nc: {coastNuoc.toLocaleString()}<br></br>Gi
                        nc:{" "}
                        {nuoctieuthu >= 0 ? `${nuoctieuthu} x 20.000` : "0"}
                      </div>
                    </div>
                    <span className={cx("divider")}>
                      --------------------------------------------------
                    </span>
                    <span className={cx("total-amount")}>
                      Tng thanh ton: {total.toLocaleString()}
                    </span>
                  </div>
                </div>
              );
            })}
        </div>
      )}
    </div>
  );
}

export default PayMentInfo;
