export function convertNumberToVietnameseText(number) {
    const ChuSo = ["khng", "mt", "hai", "ba", "bn", "nm", "su", "by", "tm", "chn"];
  
    function docSo3ChuSo(baso) {
      let tram = Math.floor(baso / 100);
      let chuc = Math.floor((baso % 100) / 10);
      let donvi = baso % 10;
      let ketQua = "";
  
      if (tram !== 0) {
        ketQua += ChuSo[tram] + " trm ";
        if (chuc === 0 && donvi !== 0) ketQua += "l ";
      }
  
      if (chuc !== 0 && chuc !== 1) {
        ketQua += ChuSo[chuc] + " mi ";
        if (donvi === 1) ketQua += "mt ";
        else if (donvi === 5) ketQua += "lm ";
        else if (donvi !== 0) ketQua += ChuSo[donvi] + " ";
      } else if (chuc === 1) {
        ketQua += "mi ";
        if (donvi === 1) ketQua += "mt ";
        else if (donvi === 5) ketQua += "lm ";
        else if (donvi !== 0) ketQua += ChuSo[donvi] + " ";
      } else if (chuc === 0 && donvi !== 0) {
        ketQua += ChuSo[donvi] + " ";
      }
  
      return ketQua.trim();
    }
  
    function toVietnamese(number) {
      if (number === 0) return "khng ng";
  
      let so = number.toString().split("").reverse().join("");
      let arr = [];
      for (let i = 0; i < so.length; i += 3) {
        arr.push(so.substring(i, i + 3).split("").reverse().join(""));
      }
  
      let chu = "";
      let donvi = ["", " nghn", " triu", " t", " nghn t", " triu t"];
      for (let i = arr.length - 1; i >= 0; i--) {
        let n = parseInt(arr[i]);
        if (n !== 0) {
          chu += docSo3ChuSo(n) + donvi[i] + " ";
        }
      }
  
      return chu.trim() + " ng";
    }
  
    return toVietnamese(number);
  }
