import React, { useEffect, useState } from "react";
import styles from "./History.module.scss";
import classNames from "classnames/bind";
import { fetchHistory } from "../../../api";

import flatpickr from "flatpickr";
import "flatpickr/dist/flatpickr.min.css";
import { formatDateValue, formatTimeValue } from "../../dateUtils";

const cx = classNames.bind(styles);

function HistoryRoom() {
  const [history, setHistory] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 5;

  useEffect(() => {
    const loadHistory = async () => {
      const getHistory = await fetchHistory('all');
      const sortedHistory = await getHistory.sort((a, b) => new Date(b.checkOutDate) - new Date(a.checkInDate));
      setHistory(sortedHistory);
    };

    loadHistory();
  }, []);
  console.log(history)

  // Tnh ton d liu cho mi trang
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentItems = history.slice(indexOfFirstItem, indexOfLastItem);

  // Chuyn sang trang k tip
  const nextPage = () => {
    if (currentPage < totalPages()) {
      setCurrentPage(currentPage + 1);
    }
  };

  // Chuyn sang trang trc
  const prevPage = () => {
    if (currentPage > 1) {
      setCurrentPage(currentPage - 1);
    }
  };

  // Tnh tng s trang
  const totalPages = () => {
    return Math.ceil(history.length / itemsPerPage);
  };


  const handleDeleteItem = async (idToDelete) => {
    try {
      // Gi yu cu xa n backend
      const response = await fetchHistory(idToDelete);

      // Kim tra nu xa thnh cng (ty vo response t backend)
      if (response.status === 200) {
        // Xa phn t khi state history
        const updatedHistory = history.filter(item => item._id !== idToDelete);
        setHistory(updatedHistory);
      } else {
        console.error("Khng th xa lch s, vui lng th li.");
      }
    } catch (error) {
      console.error("Li khi xa lch s:", error);
    }
  };


  useEffect(() => {
      flatpickr(`#startTime`, {
        enableTime: true,
        noCalendar: true,
        dateFormat: "H:i",
        time_24hr: true,
        minuteIncrement: 60,
  
      });
      flatpickr(`#endTime`, {
        enableTime: true,
        noCalendar: true,
        dateFormat: "H:i",
        time_24hr: true,
        minuteIncrement: 60,
      });
  
  
      flatpickr(`#startDate`, {
        dateFormat: "d-m-Y", // <-- nh dng ngy/thng/nm ng
        maxDate: "today",     // Chn cc ngy sau ngy hin ti
      });
  
      flatpickr(`#endDate`, {
        dateFormat: "d-m-Y", // <-- nh dng ngy/thng/nm ng
      });
  
    }, []);


  return (
    <div>
      <h1 className={cx("title-table")}>Lch S Thu Phng</h1>
      
      <table className={cx("history-table")}>
        <thead>
          <tr>
            <th>S Phng</th>
            <th>Ngy Vo</th>
            <th>Ngy Ra</th>
            <th>Tng Tin</th>
            <th>Ghi Ch</th>
            <th>Hnh ng</th>
          </tr>
        </thead>
        <tbody className={cx("data")}>
          {currentItems && currentItems.length > 0 ? (
            currentItems.map((item, index) => (
              <tr key={index}>
                <td>{item.roomNumber}</td>
                <td>
                  Ngy {formatDateValue(item.checkInDate)} {formatTimeValue(item.checkInDate).split(":")[0]} gi {formatTimeValue(item.checkInDate).split(":")[1]} pht
                </td>

                <td>
                  Ngy {formatDateValue(item.checkOutDate)} {formatTimeValue(item.checkOutDate).split(":")[0]} gi {formatTimeValue(item.checkOutDate).split(":")[1]} pht
                </td>
                <td>{item.totalAmount}</td>
                <td dangerouslySetInnerHTML={{ __html: item.description }} />
                <td><button onClick={() => handleDeleteItem(item._id)}>Xa</button></td>
              </tr>
            ))
          ) : (
            <tr>
              <td colSpan="6" style={{ textAlign: 'center', padding: '20px', fontStyle: 'italic' }}>
                Khng c bng no c ghi
              </td>
            </tr>
          )}

        </tbody>
      </table>

      {/* Pagination Controls */}
      <div className={cx("pagination")}>
        <div className={cx("notification")}><span>Trang {currentPage} of {totalPages()}</span></div>
        <div className={cx("controls")}>
          <button onClick={prevPage} disabled={currentPage === 1}>
            Trang trc
          </button>
          <button onClick={nextPage} disabled={currentPage === totalPages()}>
            Trang sau
          </button>
        </div>
      </div>
    </div>
  );
}

export default HistoryRoom;
