import React, { useEffect, useState } from "react";
import { GetAllRoomsMonth, GetUpdateRoom, deleteRoomV2 } from "api";
import { useModal } from "component/Modal/ModalContext";
import classNames from "classnames/bind";
import styles from "./Room.module.scss";
import AddRoomMonth from "./AddRoomMonth/AddRoomMonth";
import { FaTrash, FaEdit } from "react-icons/fa";
import AdjustRoomMonth from "./AdjustRoom/AdjustRoom";
import PayMentInfo from "./PaymentInfo/PaymentInfo";
import Telegram from "component/Telegram/Telegram";

const cx = classNames.bind(styles);

function ListRoom() {
  const [ListRoom, setListRoom] = useState([]);
  const { openModal } = useModal();
  const getListRoom = async () => {
    const response = await GetAllRoomsMonth();
    if (response) {
      setListRoom(response.data);
    }else{
      setListRoom([])
    }
  };

  useEffect(() => {
    getListRoom();
  }, []);

  const HandleAdjustRoom = async (id, key) => {
    const input = prompt(`Vui lng nhp ch s`);

    if (input) {
      let value;

      if (input.includes("+")) {
        const parts = input.split("+");
        const base = parseFloat(parts[0]) || 0;
        const plus = parseFloat(parts[1]) || 0;
        value = base + plus;
      } else {
        value = parseFloat(input);
      }

      if (!isNaN(value)) {
        const object = { [key]: value };
        const response = await GetUpdateRoom(id, object);
        if (response) {
          getListRoom();
        }
      }
    }
  };

  const handleOpenAddRoom = () => {
    openModal(<AddRoomMonth list={ListRoom} />);
  };

  const handleDeleteRoom = async (id) => {
    const password = prompt("Bn hy nhp mt khu  xa phng?");
    if (password !== "0786566176") {
      alert("Mt khu khng ng!");
      return;
    }

    const response = await deleteRoomV2(id);
    if (response) {
      alert("Xa phng thnh cng!");
      getListRoom();
    }
  };

  const handlePaymentInfo = () => {
    // const filterCoastRoom = ListRoom.filter((item) => item.GiaPhong !== 0);

    // const isValid = filterCoastRoom.every(
    //   (item) => item.chisodienmoi !== 0 && item.chisonuoclanhmoi !== 0
    // );
    // if (!isValid) {
    //   alert(
    //     " to ha n, hy to mi ch s in nc ca tt c cc phng"
    //   );
    //   return;
    // }

    openModal(<PayMentInfo props={ListRoom}/>);
  };


  const handleOpenAjust = (item) => {
    openModal(<AdjustRoomMonth props={item} />);
  }

  const handleOpenTelegram = () => {
    openModal(<Telegram props={1}/>)
  }

  return (
    <>
      <div>
        <div className={cx("search")}>
          <button
            onClick={(e) => handleOpenAddRoom()}
            className={cx("search-button")}
          >
            Thm phng
          </button>
          <button
            onClick={(e) => handlePaymentInfo()}
            className={cx("search-button")}
          >
            To Ha n
          </button>
          <button
            onClick={(e) => handleOpenTelegram()}
            className={cx("search-button")}
          >
            iu khin qua Telegram
          </button>
        </div>
        <table className={cx("water-table-history-table")}>
          <thead>
            <tr>
              <th>STT</th>
              <th>S Phng</th>
              <th>Gi Phng</th>
              <th>Ch s tiu th in</th>
              <th>Ch s tiu th nc</th>
              <th>Sinh hot</th>
              <th>Trng thi</th>
            </tr>
          </thead>
          <tbody className={cx("water-table-data")}>
            {ListRoom.length > 0 ? (
              ListRoom.map((item, index) => (
                <tr key={index}>
                  <td>{index + 1}</td>
                  <td>
                    <div className={cx("room-item")}>
                      <span>
                        {item.SoPhong}
                        <a
                          onClick={() => HandleAdjustRoom(item._id, "SoPhong")}
                          className={cx("plus-minus-button")}
                        >
                          +
                        </a>
                      </span>
                    </div>
                  </td>
                  <td>
                    <div className={cx("room-item")}>
                      <span>
                        {item.GiaPhong.toLocaleString()}
                        <a
                          onClick={() => HandleAdjustRoom(item._id, "GiaPhong")}
                          className={cx("plus-minus-button")}
                        >
                          +
                        </a>
                      </span>
                    </div>
                  </td>
                  <td>
                    <div className={cx("room-item")}>
                      
                      <span>
                        in mi: {item.chisodienmoi}
                        <a
                          onClick={() => HandleAdjustRoom(item._id, "chisodienmoi")}
                          className={cx("plus-minus-button")}
                        >
                          +
                        </a>
                      </span>
                    </div>
                  </td>
                  <td>
                    <div className={cx("room-item")}>
                    {item.loainuoc == "chiso" ? (
  <>
    <span>
      Nc nng mi: {item.chisonuocnongmoi}
      <a
        onClick={() => HandleAdjustRoom(item._id, "chisonuocnongmoi")}
        className={cx("plus-minus-button")}
      >
        +
      </a>
    </span>

    <span>
      Nc lnh mi: {item.chisonuoclanhmoi}
      <a
        onClick={() => HandleAdjustRoom(item._id, "chisonuoclanhmoi")}
        className={cx("plus-minus-button")}
      >
        +
      </a>
    </span>
  </>
) : (
  <>
    <span>S lng: {item.songuoikhoang}</span>
    <span>Khong {item.giakhoangnuoc.toLocaleString()}/ngi/thng</span>
  </>
)}

                    </div>
                  </td>
                  <td>
                    <div className={cx("room-item")}>
                      <span>
                        {item.sinhhoatchung.toLocaleString()}
                        <a
                          onClick={() =>
                            HandleAdjustRoom(item._id, "sinhhoatchung")
                          }
                          className={cx("plus-minus-button")}
                        >
                          +
                        </a>
                      </span>
                    </div>
                  </td>
                  <td>
                    <div className={cx("room-item")}>
                      <span>
                        <a onClick={(e) => handleOpenAjust(item)} className={cx("plus-minus-button", "icon")}>
                          <FaEdit />
                        </a>
                        <a
                          onClick={(e) => handleDeleteRoom(item._id)}
                          className={cx("plus-minus-button", "icon")}
                        >
                          <FaTrash />
                        </a>
                      </span>
                    </div>
                  </td>
                </tr>
              ))
            ) : (
              <tr>
                <td
                  colSpan="7"
                  style={{
                    textAlign: "center",
                    padding: "20px",
                    fontStyle: "italic",
                  }}
                >
                  Khng c bng no c ghi
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>
    </>
  );
}

export default ListRoom;
