import React, { useEffect, useState } from "react";
import styles from "./TotalPaymentInfo.module.scss";
import classNames from "classnames/bind";
import { useModal } from "component/Modal/ModalContext";
import Room from "component/RoomMonth/Room";

const cx = classNames.bind(styles);

function TotalPaymentInfo({ props}) {
  const [data, setData] = useState({});

  const [onlyPrice, setOnlyPrice] = useState(3)
  const {openModal} = useModal()

  useEffect(() => {
    let totalRoom = 0;
    let totalElectric = 0;
    let totalWater = 0;
    let totalElectricUsed = 0;
    let totalWaterUsed = 0;
    let roomCount = [];
    let filteredRooms;
    if (onlyPrice === 1) {
      filteredRooms = props.filter((item) => item.GiaPhong > 0);
      
    } else {
      filteredRooms = props.filter((item) => item.GiaPhong === 0);
    }

    filteredRooms.forEach((item) => {
      roomCount.push(item.SoPhong)
      const totalElectricCount = item.chisodienmoi - item.chisodiencu
      const totalWaterCount = item.loainuoc == "chiso" && (item.chisonuoclanhmoi + item.chisonuocnongmoi) - (item.chisonuocnongcu + item.chisonuoclanhcu)
    
      console.log(`S phng s dng in ${item.SoPhong}: ${totalElectricCount}`)
      console.log(`S phng s dng nc ${item.SoPhong}: ${totalWaterCount}`)

      totalElectric+=totalElectricCount
      totalElectricUsed += totalElectricCount * 4000

      totalWater += totalWaterCount
      totalWaterUsed += totalWaterCount * 20000
    });

    if(roomCount.length) {
      setData({
        totalRoom: totalRoom.toLocaleString(),
        totalElectric: totalElectric > 0 ? totalElectric : 0,
        totalWater: totalWater > 0 ? totalWater : 0,
        totalElectricUsed:
          totalElectricUsed > 0 ? totalElectricUsed.toLocaleString() : 0,
        totalWaterUsed:
          totalWaterUsed > 0 ? totalWaterUsed.toLocaleString() : 0,
        roomCount: roomCount.length
          ? roomCount.join(", ")
          : "Khng c phng no ph hp",
      });
    }else{
      setOnlyPrice(3)
    }

    
  }, [onlyPrice]); // nn thm dependency l props nu n c th thay i

  return (
    <div className={cx("container")}>
      <div className={cx("summary-item")}>
        <button onClick={(e) => setOnlyPrice(2)} className={cx("btn-summary")}>
          Tng kt ha n phng trng
        </button>
        <button onClick={(e) => setOnlyPrice(1)}  className={cx("btn-summary", "btn-total")}>
          Tng kt ha n phng hin c
        </button>
      </div>
      {onlyPrice == 3 && <div className={cx("center")}><span>Hin cha c d liu c np vui lng th li sau khi np d liu</span></div>}

{(onlyPrice === 1 || onlyPrice === 2) && (
  <div className={cx("result")}>
    <div className={cx("result-item")}>
      <strong>Phn loi</strong> {onlyPrice === 2 ? "Cc phng cha c khch" : "Cc phng c khch"}
    </div>
    <div className={cx("result-item")}>
      <strong>Tng s phng  tnh:</strong> Phng: {data.roomCount}
    </div>

    <div className={cx("result-item")}>
      <strong>Tng lng in s dng:</strong> {data.totalElectric} kWh
    </div>

    <div className={cx("result-item")}>
      <strong>Tng tin in:</strong> {data.totalElectricUsed} ng
    </div>

    <div className={cx("result-item")}>
      <strong>Tng lng nc s dng:</strong> {data.totalWater} m
    </div>

    <div className={cx("result-item")}>
      <strong>Tng tin nc:</strong> {data.totalWaterUsed} ng
    </div>

    <div className={cx("result-item")}>
      <strong>Tng tin phng (c nh):</strong> {data.totalRoom} ng
    </div>

    <div className={cx("result-item")}>
      <button onClick={(e) => openModal(<Room />)} className={cx("button")}> Quay li</button>
    </div>
  </div>
)}

    </div>
  );
}

export default TotalPaymentInfo;
