import PizZip from "pizzip";
import Docxtemplater from "docxtemplater";

const generateContract = async (data) => {
  try {
    const response = await fetch("http://localhost:3000/contract.docx");
    if (!response.ok) throw new Error("Khng th ti file DOCX");

    const blob = await response.blob();
    const contentType = blob.type;
    if (contentType !== "application/vnd.openxmlformats-officedocument.wordprocessingml.document") {
      console.error("File khng hp l");
      return;
    }

    const arrayBuffer = await blob.arrayBuffer();
    if (arrayBuffer.byteLength === 0) {
      console.error("File rng hoc hng");
      return;
    }

    const zip = new PizZip(arrayBuffer);
    const doc = new Docxtemplater(zip, { paragraphLoop: true, linebreaks: true });

    doc.setData(data);
    doc.render();

    const out = doc.getZip().generate({ type: "blob" });
    const url = URL.createObjectURL(out);
    const a = document.createElement("a");
    a.href = url;
    a.download = "HopDongDaDien.docx";
    document.body.appendChild(a);
    a.click();
    a.remove();
    URL.revokeObjectURL(url);
  } catch (error) {
    console.error("Li to hp ng:", error);
  }
};

export default generateContract;
