import React, { useState } from "react";
import styles from "./AddRoom.module.scss";
import classNames from "classnames/bind";
import { addRoom } from "api";


const cx = classNames.bind(styles);

function AddRoom() {

  const [data, setData] = useState({
    roomNumber: "",
    dailyprice: "",
    firstHour: "",
    extraHour: ""
  });
  const [message, setMessage] = useState("");

  const handleAddRoom = async (event) => {
    
  
    const { roomNumber, dailyprice, firstHour, extraHour } = data;
  
    if (!roomNumber || !dailyprice || !firstHour || !extraHour) {
      setMessage("Vui lng nhp y  thng tin!");
      return;
    }
  
    try {
      const response = await addRoom(data);
      if (response) {
        setMessage("Thm phng thnh cng!");
        // Reset form nu cn
        // setData({ roomNumber: '', dailyprice: '', firstHour: '', extraHour: '', ... });
      }
    } catch (error) {
      console.error(error); // log ra  d debug
      setMessage(error.message || "C li xy ra khi thm phng!");
    }
  };


  return (
    <div className={cx("container")}>
    <h2 className={cx("title")}>Thm Phng Mi</h2>
    <form className={cx("form")} id="roomForm">
      
      <div className={cx("row")}>
        <div className={cx("formGroup")}>
          <label className={cx("label")}>S phng:</label>
          <input
            value={data.roomNumber || ''}
            onChange={(e) => setData((prev) => ({ ...prev, roomNumber: e.target.value }))}
            type="number"
            id="roomNumber"
            required
            className={cx("input")}
          />
        </div>
  
        <div className={cx("formGroup")}>
          <label className={cx("label")}>Gi phng ngy:</label>
          <input
            value={data.dailyprice || ''}
            onChange={(e) => setData((prev) => ({ ...prev, dailyprice: e.target.value }))}
            type="number"
            id="daily"
            required
            className={cx("input")}
          />
        </div>
      </div>
  
      <div className={cx("row")}>
        <div className={cx("formGroup")}>
          <label className={cx("label")}>Gi u tin:</label>
          <input
            value={data.firstHour || ''}
            onChange={(e) => setData((prev) => ({ ...prev, firstHour: e.target.value }))}
            type="number"
            id="firstHour"
            required
            className={cx("input")}
          />
        </div>
  
        <div className={cx("formGroup")}>
          <label className={cx("label")}>Gi thm gi:</label>
          <input
            value={data.extraHour || ''}
            onChange={(e) => setData((prev) => ({ ...prev, extraHour: e.target.value }))}
            type="number"
            id="extraHour"
            required
            className={cx("input")}
          />
        </div>
      </div>
  
      <div className={cx("control")}>
        <button type="button" onClick={() => handleAddRoom()} className={cx("button")}>Thm Phng</button>
      </div>
    </form>
    <p id="message" className={cx("message")}>{message}</p>
  </div>
  
  );
}

export default AddRoom;