import axios from "axios";

const api = axios.create({
  baseURL: "http://localhost:3000",
  timeout: 10000,
});

// Hm x l promise chung c catch li
const handleRequest = async (promise, errorMessage) => {
  try {
    const res = await promise;
    return res.data;
  } catch (error) {
    const message = error.response?.data?.message || errorMessage || "C li xy ra!";
    throw new Error(message);
  }
};

// Cc API di y:

const editRoom = (room, adjust) => {
  return handleRequest(
    api.post('/api/edit-room', {
      roomNumber: room,
      newDetails: adjust
    }),
    "Li khi chnh sa phng"
  );
};

const addRoom = (object) => {
  return handleRequest(
    api.post('/api/addroom', {
      roomNumber: object.roomNumber,
      daily: object.dailyprice,
      firstHour: object.firstHour,
      extraHour: object.extraHour,
      checkInDate: "",
      checkOutDate: "",
    }),
    "Li khi thm phng"
  );
};

const saveRoomHistory = (room, adjust) => {
  return handleRequest(
    api.post('/api/history', {
      roomNumber: room,
      totalAmount: adjust.totalAmount,
      checkInDate: adjust.checkInDate,
      checkOutDate: adjust.checkOutDate,
      description: adjust.description,
    }),
    "Li khi lu lch s phng"
  );
};

const deleteRoomData = (room) => {
  return handleRequest(
    api.get(`/api/reset/${room}`),
    "Li khi reset phng"
  );
};

const fetchRoomsList = async () => {
  const data = await handleRequest(
    api.get("/api/rooms"),
    "Li khi ly danh sch phng"
  );
  if (data) {
    localStorage.setItem("rooms", JSON.stringify(data));
  }
  return data;
};

const fetchHistory = (type) => {
  const url = type === 'all' ? "/api/history/all" : `/api/history/${type}`;
  return handleRequest(api.get(url), "Li khi ly lch s phng");
};

const fetchWaterList = () => {
  return handleRequest(
    api.get("/api/water"),
    "Li khi ly danh sch nc"
  );
};

const updateWater = (object) => {
  return handleRequest(
    api.post('/api/update-water', {
      tenNuoc: object.tenNuoc,
      soLuong: Number(object.soLuong) || 0,
      nhap: Number(object.nhap) || 0,
    }),
    "Li khi cp nht nc"
  );
};

const TestInventory = () => {
  return handleRequest(
    api.get("/api/kiemhangthuall"),
    "Li khi kim hng th"
  );
};

const TestInventory2 = async () => {
  const check = window.confirm("Bn c chc mun to mi d liu khng?\nH thng s t ng ly S lng cui v Chuyn v li S lng u");
  if (!check) return null;
  
  return handleRequest(
    api.get("/api/resetwt"),
    "Li khi reset s lng nc"
  );
};

const GetAllRoomsMonth = () => {
  return handleRequest(
    api.get("/api/getallroom/v2"),
    "Li khi ti danh sch phng thng"
  );
};

const GetUpdateRoom = (id, object) => {
  return handleRequest(
    api.put(`/api/update/${id}/v2`, object),
    "Li khi cp nht phng"
  );
};

const addRoomV2 = (object) => {
  return handleRequest(
    api.post(`/api/addroom/v2`, {
      hovaten: object.hovaten,
      SoPhong: object.SoPhong,
      GiaPhong: object.GiaPhong,
      chisodiencu: object.chisodiencu || 0,
      chisodienmoi: 0,
      chisonuoclanhcu: object.chisonuoclanhcu || 0,
      chisonuoclanhmoi: 0,
      chisonuocnongcu: object.chisonuocnongcu || 0,
      chisonuocnongmoi: 0,
      sinhhoatchung: object.sinhhoatchung || 0,
      ghichu: "Cha c ghi ch",
      loainuoc: object.loainuoc,
      songuoikhoang: object.nguoi || 0,
      giakhoangnuoc: 90000,
    }),
    "Li khi thm phng mi"
  );
};

const deleteRoomV2 = async (id) => {
  const check = window.confirm("Bn c chc mun xa khng?");
  if (!check) return null;

  return handleRequest(
    api.get(`/api/delete/${id}/v2`),
    "Li khi xa phng"
  );
};

// Xut ton b
export { 
  api, 
  editRoom, 
  saveRoomHistory, 
  deleteRoomData, 
  fetchRoomsList, 
  fetchHistory, 
  addRoom, 
  fetchWaterList, 
  updateWater, 
  TestInventory, 
  TestInventory2, 
  GetAllRoomsMonth, 
  GetUpdateRoom, 
  addRoomV2, 
  deleteRoomV2 
};
