import React, { useState, useEffect, useRef } from "react";
import Room from "./Room";
import {
  fetchRoomsList,
  editRoom,
  saveRoomHistory,
  deleteRoomData,
} from "../../api"; // Gi s hm ny s fetch d liu t API
import classNames from "classnames/bind";
import styles from "./Room.module.scss";
import { convertToStandardDateFormat, convertToISODate } from "../dateUtils";
import { useModal } from "../Modal/ModalContext";


const cx = classNames.bind(styles);

const RoomList = () => {
  const [rooms, setRooms] = useState([]);
  const depositRef = useRef(null);
  const deposit2Ref = useRef(null);

  const { openModal, closeModal } = useModal();

  useEffect(() => {
    const loadRooms = async () => {
      const fetchedRooms = await fetchRoomsList();
      const sortedRooms = fetchedRooms.sort(
        (a, b) => a.roomNumber - b.roomNumber
      );
      setRooms(sortedRooms);
    };

    loadRooms();
  }, []);

  const handleInputChange = async (roomNumber, data) => {
    let date;
    if (data.type === "checkTimeDate") {
      date = data.date + " " + data.value;
    } else {
      date = data.value + " " + data.time;
    }
    await editRoom(String(roomNumber), { checkInDate: date });
  };

  const setCurrentTime = async (type, roomNumber, ref) => {
    const now = new Date();
    const dateStr = now.toLocaleDateString("vi-VN"); // "24/04/2025"
    const hours = String(now.getHours()).padStart(2, "0");
    const minutes = String(now.getMinutes()).padStart(2, "0");
    const timeStr = `${hours}:${minutes}`;

    const dateStrBK = convertToStandardDateFormat(dateStr, timeStr); // GI NGUYN LOGIC

    if (type === "checkin") {
      const checkinDateInput = ref.current[roomNumber]["checkindate"];
      const checkinTimeInput = ref.current[roomNumber]["checkintime"];

      if (checkinDateInput && checkinDateInput._flatpickr) {
        checkinDateInput.value = dateStr; // Hin th trc tip
        checkinDateInput._flatpickr.setDate(dateStr, true, "d-m-Y");
      }

      if (checkinTimeInput && checkinTimeInput._flatpickr) {
        checkinTimeInput.value = timeStr;
        checkinTimeInput._flatpickr.setDate(timeStr, true, "H:i");
      }

      const response = await editRoom(String(roomNumber), {
        checkInDate: dateStrBK,
      });
      if (response) return window.location.reload();

      
    } else {
      const checkoutDateInput = ref.current[roomNumber]["checkoutdate"];
      const checkoutTimeInput = ref.current[roomNumber]["checkouttime"];

      if (checkoutDateInput && checkoutDateInput._flatpickr) {
        checkoutDateInput.value = dateStr;
        checkoutDateInput._flatpickr.setDate(dateStr, true, "d-m-Y");
      }

      if (checkoutTimeInput && checkoutTimeInput._flatpickr) {
        checkoutTimeInput.value = timeStr;
        checkoutTimeInput._flatpickr.setDate(timeStr, true, "H:i");
      }

     
    }
  };

  const calculate = (roomNumber, data) => {
    const ref = data;
    const checkInDate = convertToISODate(
      ref.current[roomNumber]["checkindate"].value
    );
    const checkInTime = ref.current[roomNumber]["checkintime"].value;
    const checkOutDate = convertToISODate(
      ref.current[roomNumber]["checkoutdate"].value
    );
    const checkOutTime = ref.current[roomNumber]["checkouttime"].value;

    if (!checkInDate || !checkInTime || !checkOutDate || !checkOutTime) {
      return openModal(
        <p style={{ textAlign: "center" }}>Vui lng nhp y  thi gian.</p>
      );
    }

    const start = new Date(`${checkInDate}T${checkInTime}`);
    const end = new Date(`${checkOutDate}T${checkOutTime}`);

    if (end <= start) {
      return openModal(
        <p style={{ textAlign: "center" }}>
          Thi gian tr phng cn c thit lp mun hn thi gian nhn phng
           m bo hp l.
        </p>
      );
    }

    let total = 0;
    const notes = [];
    const diffMs = end - start;
    const diffMinutes = Math.ceil(diffMs / 60000);
    const diffHours = Math.ceil(diffMs / 3600000);
    const startHour = start.getHours();
    const endHour = end.getHours();
    const isSameDay = start.toDateString() === end.toDateString();
    const isNightStay =
      startHour >= 19 &&
      startHour <= 23 &&
      endHour >= 0 &&
      endHour <= 2 &&
      diffHours <= 9;

    const dataRoom = rooms.find((room) => room.roomNumber === roomNumber);

    // Danh sch ngy l
    const holidays = {
      "01-01": "Tt Dng Lch",
      "30-04": "Gii phng min Nam",
      "01-05": "Quc t Lao ng",
      "02-09": "Quc khnh",
    };

    // Hm m ngy l
    function getHolidayDays(start, end) {
      const holidayCounts = {};
      const current = new Date(start);
      current.setHours(0, 0, 0, 0);
      const endDate = new Date(end);
      endDate.setHours(0, 0, 0, 0);

      while (current <= endDate) {
        const currentStr = current
          .toLocaleDateString("vi-VN", { day: "2-digit", month: "2-digit" })
          .replace(/\//g, "-");
        if (holidays[currentStr]) {
          holidayCounts[holidays[currentStr]] =
            (holidayCounts[holidays[currentStr]] || 0) + 1;
        }
        current.setDate(current.getDate() + 1);
      }
      return holidayCounts;
    }

    // ----- Tnh gi -----
    let surcharge = 0;
    let holidaySurcharge = 0;

    if (isSameDay || isNightStay) {
      // Thu theo gi
      total = dataRoom.firstHour;
      notes.push(`Gi u: ${dataRoom.firstHour.toLocaleString()} VN`);

      const extraMinutes = diffMinutes - 60;
      const extraHoursCharged =
        extraMinutes > 0 ? Math.floor((extraMinutes + 45) / 60) : 0;

      if (extraHoursCharged > 0) {
        let temp = new Date(start);
        temp.setMinutes(temp.getMinutes() + 60); // B 1 gi u

        let totalExtraHourSurcharge = 0;
        let holidayHours = 0;

        for (let i = 0; i < extraHoursCharged; i++) {
          const currentStr = temp
            .toLocaleDateString("vi-VN", { day: "2-digit", month: "2-digit" })
            .replace(/\//g, "-");
          const isHoliday = holidays[currentStr];
          const hourPrice = dataRoom.extraHour + (isHoliday ? 10000 : 0);
          totalExtraHourSurcharge += hourPrice;
          if (isHoliday) holidayHours++;
          temp.setHours(temp.getHours() + 1);
        }

        notes.push(
          `Ph thu: ${extraHoursCharged} gi x ${dataRoom.extraHour.toLocaleString()} VN ${
            holidayHours > 0 ? `(Ngy l: ${holidayHours} gi)` : ""
          } = ${totalExtraHourSurcharge.toLocaleString()} VN`
        );
        total += totalExtraHourSurcharge;
      }
    } else {
      // Thu qua m
      if (startHour >= 5 && startHour <= 12) {
        const surchargeHours = 12 - startHour;
        if (surchargeHours > 0) {
          surcharge += surchargeHours * dataRoom.extraHour;
          notes.push(
            `Ph thu nhn phng sm: ${surchargeHours} gi x ${dataRoom.extraHour.toLocaleString()} VN`
          );
        }
      }

      const firstDayEnd = new Date(start);
      firstDayEnd.setDate(firstDayEnd.getDate() + 1);
      firstDayEnd.setHours(12, 0, 0, 0);

      if (end <= firstDayEnd) {
        total = dataRoom.daily;
        notes.push(`Tnh 1 ngy: ${dataRoom.daily.toLocaleString()} VN`);
      } else {
        const lastNoon = new Date(end);
        lastNoon.setHours(12, 0, 0, 0);
        const fullDays = Math.round((lastNoon - firstDayEnd) / 86400000) + 1;
        total = fullDays * dataRoom.daily;
        notes.push(`${fullDays} ngy x ${dataRoom.daily.toLocaleString()} VN`);
      }

      if (endHour >= 12) {
        const extraHours = endHour - 12;
        if (extraHours > 0) {
          surcharge += extraHours * dataRoom.extraHour;
          notes.push(
            `Ph thu tr phng mun: ${extraHours} gi x ${dataRoom.extraHour.toLocaleString()} VN`
          );
        }
      }
    }

    let holidayHours = 0;
    // -------- Tnh ph thu l --------
    if (!isSameDay) {
      const holidayCounts = getHolidayDays(start, end);
      for (const name in holidayCounts) {
        holidaySurcharge += holidayCounts[name] * 100000;
        notes.push(`Qua m ngy l ${name}: +100,000 VN`);
      }

      if (endHour >= 12) {
        const extraHours = endHour - 12;
        if (extraHours > 0) {
          const temp = new Date(start);
          while (temp <= end) {
            const currentStr = temp
              .toLocaleDateString("vi-VN", { day: "2-digit", month: "2-digit" })
              .replace(/\//g, "-");
            if (holidays[currentStr]) {
              holidayHours = extraHours;
              holidaySurcharge += extraHours * 10000;
              break; // ch cn 1 ln gp ngy l l cng
            }
            temp.setHours(temp.getHours() + 1);
          }
        }
      }
    } 

    // Ghi ch tng s gi ph thu l
    if (holidayHours > 0) {
      notes.push(
        `Ph thu ${holidayHours} gi l x 10,000 VN = ${(
          holidayHours * 10000
        ).toLocaleString()} VN`
      );
    }

    // 3. Ghi ch tng ph thu l
    if (holidaySurcharge > 0) {
      notes.push(`Tng ph thu l: +${holidaySurcharge.toLocaleString()} VN`);
    }

    // ----------------- Kt thc -----------------
    total += surcharge + holidaySurcharge;

    // Ghi ch thi gian  thc
    notes.push(
      `Thi gian  thc: ${
        diffMinutes >= 1440
          ? `${Math.floor(diffMinutes / 1440)} ngy${
              diffMinutes % 1440
                ? ` ${Math.floor((diffMinutes % 1440) / 60)} gi ${
                    diffMinutes % 60
                  } pht`
                : ""
            }`
          : `${Math.floor(diffMinutes / 60)} gi ${diffMinutes % 60} pht`
      }`
    );

    const objectDatatoSaveHistory = {
      totalAmount: total.toLocaleString(),
      checkInDate: start.toLocaleString(),
      checkOutDate: end.toLocaleString(),
      description: notes.map((note) => `<span>${note}</span>`).join(""),
    };

    const HandleSaveDatatoHistory = async () => {
      ref.current[roomNumber]["checkindate"].value = "";
      ref.current[roomNumber]["checkintime"].value = "";
      ref.current[roomNumber]["checkoutdate"].value = "";
      ref.current[roomNumber]["checkouttime"].value = "";
      deleteRoomData(roomNumber);
      await saveRoomHistory(roomNumber, objectDatatoSaveHistory);
      closeModal();
    };

    const handleDeposit = () => {
      console.log(depositRef);
      const rawValue = depositRef.current.value.replace(/\./g, "");
      const numberValue = parseInt(rawValue, 10) || 0;
      const formatted = numberValue.toLocaleString();
      depositRef.current.value = formatted;
      deposit2Ref.current.innerText =
        (numberValue - total).toLocaleString() < 0
          ? 0
          : (numberValue - total).toLocaleString();
    };
    openModal(
      <div className={cx("calculate")}>
        <h3 className={cx("calculate-header")}>Chi tit tnh tin</h3>
        <p className={cx("room-info")}>
          <span className={cx("title")}>Phng:</span> <span>{roomNumber}</span>
        </p>
        <p className={cx("check-in-time")}>
          <span className={cx("title")}>Nhn phng:</span>{" "}
          <span>{start.toLocaleString()}</span>
        </p>
        <p className={cx("check-out-time")}>
          <span className={cx("title")}>Tr phng:</span>{" "}
          <span>{end.toLocaleString()}</span>
        </p>
        {notes.map((note, i) => (
          <p className={cx("additional-note")} key={i}>
            <span>{note}</span>
          </p>
        ))}
        <p className={cx("total-amount")}>
          <span>Tng tin:</span> <span>{total.toLocaleString()} VN</span>
        </p>
        <p className={cx("total-amount")}>
          <span>Khch a:</span>{" "}
          <input
            ref={depositRef}
            type="text"
            onChange={(e) => handleDeposit(e.target.value)}
            placeholder="Nhp s tin khch hng"
          />
          <span>
            Thi li khch: <b ref={deposit2Ref}>0</b>
          </span>
        </p>
        <div className={cx("control")}>
          <button
            className={cx("button")}
            onClick={() => HandleSaveDatatoHistory()}
          >
            Lu d liu
          </button>
          <button className={cx("button")} onClick={() => closeModal()}>
            Tm tnh
          </button>
        </div>
        <div className={cx("footer-note")}>
          <span>Phn mm c to bi Hu c</span>
        </div>
      </div>
    );
  };

  return (
    <div id={cx("room")}>
      {rooms && rooms.length > 0 ? (
        rooms.map((room) => (
          <Room
            key={room.roomNumber}
            room={room}
            handleInputChange={handleInputChange}
            setCurrentTime={setCurrentTime}
            calculate={calculate}
          />
        ))
      ) : (
        <span>Hin ti cha c phng no c thm vo d liu</span>
      )}
    </div>
  );
};

export default RoomList;
