import React from 'react';
import styles from './Header.module.scss';
import classNames from 'classnames/bind';
import HistoryRoom from 'component/RoomDaily/History/HistoryRoom';
import ListRoomDetail from 'component/RoomDaily/ListRoomDetail/ListRoomDetail';
import ListRoom from 'component/RoomMonth/Room';
import { useModal } from 'component/Modal/ModalContext';
import ListWater from 'component/WaterManager/ListWater';
import Bill from 'component/Bill/Bill';


const cx = classNames.bind(styles);

const Header = () => {
    const { openModal } = useModal();
    const handleOpenHistoryRoom = () => {
        openModal(<HistoryRoom />);
    }

    const handleOpenListRoom = () => {  
        openModal(<ListRoomDetail />);
    }

    const handleOpenWaterManager = () => {  
        //openModal(<ListWater />);//
        openModal(<Bill />)
    }


    const handleOpenListRoomMonth = () => {  
        openModal(<ListRoom />);
    }

    return (
        <header className={cx('header')}>
            <nav className={cx('header__nav')}>
                <ul className={cx('header__menu')}>
                    <li className={cx('header__item')}>
                        <a href="/" className={cx('header__link')}>Trang ch</a>
                    </li>
                    <li className={cx('header__item')}>
                        <a className={cx('header__link')} onClick={() => handleOpenHistoryRoom()}  >Lch s</a>
                    </li>
                    <li className={cx('header__item')}>
                        <a className={cx('header__link')} onClick={() => handleOpenListRoom()}>Danh sch phng ngy</a>
                    </li>
                    <li className={cx('header__item')}>
                        <a className={cx('header__link')} onClick={() => handleOpenListRoomMonth()}>Danh sch phng thng</a>
                    </li>
                    <li className={cx('header__item')}>
                        <a className={cx('header__link')} onClick={() => handleOpenWaterManager()}>Qun l nc</a>
                    </li>
                   
                </ul>
            </nav>
        </header>
    );
};

export default Header;